@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400;1,700&Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');


$primary-color: #AA0601;
$secondary-color: #FBF2F2;
$tertiary-color: #111619;

$primary-font-color: #650300;

$header-height: 108px;
$tablet-header-height: 108px;
$mobile-header-height: 108px;

$footer-height: 108px;
$tablet-footer-height: 108px;
$mobile-footer-height: 108px;


html,body{
	width: 100%;
	height: 100%;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
	font-size: 16px;
	line-height: 24px;
	font-family: 'Noto Sans', Roboto, "Helvetica Neue", Arial, Helvetica, sans-serif;
	color: $primary-font-color;
}

.eng{
	font-family:  Roboto,'Noto Sans', "Helvetica Neue", Arial, Helvetica, sans-serif;
}

h1{
	font-size: 64px;
	line-height: 96px;
	font-weight: bold;
}

h2{
	font-size: 48px;
	line-height: 72px;
	font-weight: bold;
}

h3{
	font-size: 24px;
	line-height: 36px;
	font-weight: bold;
}

a {
	text-decoration: none;
}


.header {
  background-color: $primary-color;
  padding: 30px 88px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-height: $header-height;

  .logo {
  	width: 80px;
  	height: 49px;
  }

  .menu .menu-item {
  	margin: 0 32px;
  	float: left;
  	a {
  		color: white;
  		font-size: 20px;
  	}
  }

}

///////////////
// index
///////////////

.container{
	width: 1110px;
	margin: 0 auto;
	// background-color:black;
}


.banner{
	width: 100%;
	height: calc( 100% - #{$header-height} );
	background-image: url('../images/banner.png');
	background-position: center;
	background-repeat: no-repeat;
	
	.container{
		position:relative;
	}
	.banner-text{
		text-align: right;
		
		display: inline;
		position: absolute;
		top: 348px;
		right: 0;
		.banner-subtitle{
			font-size: 36px;
			line-height: 54px;
		}
	}
}

.profession{
	background: #AA06010D;
	padding: 60px 0 80px 0;
	display: flex;
	flex-direction: column;
	align-items: center;

	.container{
		display: flex;
		justify-content: space-between;
		margin-top: 40px;

		.content-div{
			max-width: 255px;
			text-align: center;

			.icon{
				width: 100px;
				height: 100px;
				margin-bottom: 16px;
			}

			.title{
				margin-bottom: 8px;
			}

			.desc{
				color: black;
				text-align: left;
			}
		}
	}
}



.footer {
	background-color: $primary-color;
	padding:32px 0;

	.footer-1{
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 24px;

		.menu .menu-item {
	  	float: left;
	  	margin-right:48px;
	  	a {
	  		color: white;
	  		font-size: 16px;
	  		line-height: 24px;
	  	}
	  }

	  .social .social-item{
	  	float: left;
	  	margin-left: 32px;

	  	.social-icon{
	  		width:48px;
	  		height:48px;
	  	}
	  }
	}

	.footer-2,.footer-3{
		margin-bottom: 24px;
		
		img{
			width: 36px;
			height: 36px;
			margin-right: 20px;
		}

		a{
			color: white;
			font-size: 36px;
			line-height: 54px;
		}
	}

	.hr{
		height: 1px;
		border-top: 1px solid #fff;
		margin-bottom: 32px;
	}

	.footer-4{
		display:  flex;
		justify-content:  space-between;

		.copyright{
			color: white;
			font-size: 16px;
			line-height: 24px;
		}

		.privacy-policy{
			color:  white;
			margin-left:auto;
			margin-right: 48px;
		}

		.privacy-policy,.services{
			color:  white;
			font-size: 14px;
			line-height: 21px;
		}
	}
}



// 平板
@media(max-width: 1199px){
  .header {
    height: 300px;
    background-color: #004AFF;
  }
}

